.navbar {
  background: #ffffff;
  grid-area: nav;
  background: rgb(29, 29, 100);

  height: 70px;
  display: flex;
  align-items: center;
  /* align-content: center; */
  justify-content: space-between;
  padding: 0 30px 0 30px;
  /* overflow:auto; */
  /* border-bottom: 1px solid #fc1100; */
}
.navbar p {
  padding: 2px;
  /* overflow:hidden; */
  /* display:grid; */
  /* place-content: center;
    place-items: center; */
  /* border */
  /* overflow:hidden; */
  margin: auto;
  font-size: 30px;
  text-transform: uppercase;
  /* display: inline; */
  color: white;
  /* font: red !important; */
}
.nav_icon {
  display: none;
}
