/*  styling scrollbars  */
::-webkit-scrollbar {
  width: 8px;
  height: 6px;
}

::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #a5aaad;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background:rgb(151, 145, 145);
  border-radius: 10px;
}

::-webkit-scrollbar-thumb:hover {
cursor: pointer;
}

