.admitCard {
  display: grid;
  grid-template-areas:
    "top top top"
    "middle middle middle"
    "bottom bottom bottom";
  grid-gap: 10px;
  overflow: auto;
  background-color: rgb(24, 24, 65);
  padding: 10px;
  width: 600px;
  padding-bottom: 30px;
  /* height:300px; */
}
.admitCardTop {
  grid-area: top;
  /* border: 2px solid red; */
  display: grid;
  background: rgb(24, 24, 77);
  grid-template-columns: 0.1fr 1fr;
}
