  width: 90px;
  height: 80px;
}
.admitCardMiddle {
  grid-area: middle;
  /* border: 2px solid red; */
  background: rgb(24, 24, 69);
  padding: 0 20px 20px 20px;
}
.admitCardBottom {
  grid-area: bottom;
  /* border: 2px solid red; */
  background: rgb(24, 24, 71);
  padding: 3px;
}

